
import { Component, OnInit, ViewChild, ElementRef, NgZone } from '@angular/core';

import {  ModalController , NavParams} from '@ionic/angular';


@Component({
  selector: 'app-my-modalpicker',
  templateUrl: './my-modalpicker.page.html',
  styleUrls: ['./my-modalpicker.page.scss'],
})
export class MyModalpickerPage implements OnInit {


  tituloxx: any;
  idopcion: any;
  @ViewChild('search', {static: false})
  public searchElementRef: ElementRef;


  constructor( private modalController: ModalController,
    private ngZone: NgZone,
    private navParams: NavParams
  ) { }

  setColor2(op) {
    
    this.modalController.dismiss({color:  op});
  

}
  setColor(op) {
    
      this.modalController.dismiss({color:  op});
    

}
  ngOnInit() {
    this.tituloxx = this.navParams.data.record2.titulo;
    this.idopcion = this.navParams.data.record2.opcion;



  }



  async close() {
    this.modalController.dismiss('close');
  }

}
